/** Media Queries **/
.contenedor-servicios {
  border-bottom: 1.5px solid #1a1b15;
  display: flex;
  justify-content: space-between;
}

.resumen {
  background-color: rgb(226, 225, 225);
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  overflow-y: auto;
  margin: 2rem auto;
  max-height: 40rem;
}
.resumen p {
  margin-bottom: 1rem;
  font-size: 1.8rem;
}

.dato {
  text-align: center;
}

.total {
  text-align: end;
}